import { Toast } from '@elastic/eui/src/components/toast/global_toast_list';
import { useState } from 'react';

type ToastColor = 'primary' | 'success' | 'warning' | 'danger';
let toastId = 0;

export const useToast = () => {
  const [toasts, setToasts] = useState([] as Toast[]);

  const removeToast: (removedToast: Toast) => void = (removedToast: Toast) => {
    setToasts(toasts.filter((toast) => toast.id !== removedToast.id));
  };

  function showToast(color: ToastColor, title?: string, text?: string) {
    setToasts(
      toasts.concat({
        id: '' + toastId++,
        title: title,
        text: text,
        color: color,
        iconType: getToastIcon(color),
      } as Toast)
    );
  }

  function getToastIcon(color: ToastColor): string | undefined {
    switch (color) {
      case 'success':
        return 'check';
      case 'warning':
        return 'indexSettings';
      case 'danger':
        return 'alert';
      default:
        return undefined;
    }
  }

  return { toasts, showToast, removeToast };
};
