import { useEffect } from 'react';

export const sendCloseSettingsMessage = () => {
  window.parent.postMessage({ settingsIframe: 'REMOVE' }, window.location.origin);
};

export const useHandleSettingsIframeMessage = (handleMessageReceived: () => void) => {
  useEffect(() => {
    function onMessageReceived(event: MessageEvent) {
      const data = event.data;
      if (data?.settingsIframe === 'CLOSE_EVENT_DISPATCHED') {
        handleMessageReceived();
      }
    }

    window.addEventListener('message', onMessageReceived, false);

    return function cleanup() {
      window.removeEventListener('message', onMessageReceived, false);
    };
  }, [handleMessageReceived]);
};
