import React, { useState } from 'react';
import {
    EuiButton,
    EuiCard,
    EuiCode,
    EuiFlexGrid,
    EuiFlexGroup,
    EuiFlexItem,
    EuiGlobalToastList,
    EuiHorizontalRule,
    EuiIcon,
    EuiPanel,
    EuiSpacer,
    EuiText,
    EuiTitle,
} from '@elastic/eui';
import AuditExample from '../../assets/images/audit_log_viz_example.png';
import TimeByUser from '../../assets/images/time_by_user_example.png';
import AvgRequestTime from '../../assets/images/avg_request_time_example.png';
import IndicesBreakdown from '../../assets/images/indices_breakdown_example.png';
import ResponseTimeVsRequestBodyLength from '../../assets/images/response_time_vs_request_body_length_example.png';
import { HorizontalCard } from '../../shared/horizontalCard';
import { importAuditLogDashboard } from '../../api/pkpClient';
import {
    sendCloseSettingsMessage,
    useHandleSettingsIframeMessage,
} from '../../shared/hooks/useHandleSettingsIframeMessage';
import { useToast } from '../../shared/hooks/useToast';
import { handleApiResponseError } from '../../api/utils';
import errors from '../../constants/errors';
import messages from '../../constants/messages';
import { OverwriteAuditLogsModal } from './overwriteAuditLogsModal';

export default function Audit() {
    const [openAuditLogsModal, setOpenAuditLogsModal] = useState<boolean>(false);
    const { toasts, showToast, removeToast } = useToast();

    useHandleSettingsIframeMessage(sendCloseSettingsMessage);

    const handleLoadAuditLogs = async (overwrite: boolean) => {
        const handle200ResponseErrorState = (
            message: string | undefined,
            responseErrors: { error: { type: 'conflict' | 'missing_references' } }[]
        ) => {
            const showOverrideDashboardDialog = (responseErrors ?? []).some(
                (responseError) => responseError.error.type === 'conflict'
            );
            if (showOverrideDashboardDialog) {
                setOpenAuditLogsModal(true);
            } else {
                showToast('danger', errors.failedToSaveSecuritySettings, message ?? '');
            }
        };

        try {
            const fileName = 'audit_dashboard.ndjson';
            const response = await importAuditLogDashboard(fileName, fileName, overwrite);
            if (response.data.success) {
                showToast('success', messages.successfullyLoadedAuditDashboard);
            } else {
                handle200ResponseErrorState(response.data?.message, response.data.errors);
            }
        } catch (e) {
            handleApiResponseError(e, {
                handleNon200Error: (error) => {
                    showToast(
                        'danger',
                        errors.failedToSaveSecuritySettings,
                        error.response?.data?.message ?? ''
                    );
                },
                handle404Error: (error) => {
                    showToast('danger', errors.elasticSearchError, error.message);
                },
                handleDefaultError: (error) => {
                    showToast('danger', errors.elasticSearchError, error.message);
                },
            });
        }
    };

    return (
        <EuiFlexGrid gutterSize={'m'} columns={2}>
            <EuiFlexItem>
                <EuiCard
                    layout="vertical"
                    textAlign="left"
                    image={
                        <img
                            style={{ padding: '10px' }}
                            src={AuditExample}
                            alt="audit-log-example"
                        />
                    }
                    title="Audit log dashboard"
                    description="Clearly express and visualise security events persisted in audit log within your cluster."
                    footer={
                        <EuiFlexGroup justifyContent="flexEnd">
                            <EuiFlexItem grow={false}>
                                <EuiButton onClick={() => handleLoadAuditLogs(false)}>
                                    Load data
                                </EuiButton>
                            </EuiFlexItem>
                        </EuiFlexGroup>
                    }
                />
            </EuiFlexItem>
            <EuiFlexItem grow={2}>
                <EuiPanel grow={true} paddingSize="m" className="euiPageContent--horizontalCenter">
                    <div className="eui-textCenter">
                        <EuiIcon size="xl" type={'gear'} />
                        <EuiTitle size="m">
                            <h3>Audit log configuration</h3>
                        </EuiTitle>
                        <EuiHorizontalRule />
                    </div>
                    <div className="eui-textLeft">
                        <EuiText>
                            <p>
                                ReadonlyREST can be configured to store incoming HTTP requests
                                events as JSON documents in a series of indices:
                                <EuiCode>readonlyrest_audit-YYYY-MM-DD</EuiCode>
                            </p>
                        </EuiText>
                        <EuiSpacer size="l" />
                        <EuiText>
                            <p>
                                To enable audit logging just add the{' '}
                                <EuiCode>audit_collector: true</EuiCode> to your security settings.
                            </p>
                            <pre>
                                <p style={{ textAlign: 'start', marginBottom: 0 }}>
                                    <EuiCode>
                                        readonlyrest:
                                        <br />
                                        <br />
                                        &nbsp;&nbsp;&nbsp;&nbsp;audit_collector: true
                                        <br />
                                        &nbsp;&nbsp;&nbsp;&nbsp;...
                                    </EuiCode>
                                </p>
                            </pre>
                        </EuiText>
                    </div>
                </EuiPanel>
            </EuiFlexItem>
            <EuiFlexItem grow={2}>
                <EuiPanel>
                    <EuiFlexGroup>
                        <EuiFlexItem>
                            <EuiTitle>
                                <h3>ReadonlyREST Audit logs example visualizations</h3>
                            </EuiTitle>
                            <EuiText size="s">
                                <p>
                                    Here are some examples of what can be done - feel free to get
                                    inspired
                                </p>
                            </EuiText>
                        </EuiFlexItem>
                    </EuiFlexGroup>
                    <EuiSpacer size="xl" />

                    <HorizontalCard
                        image={TimeByUser}
                        description="This is an example of a time by user pie chart"
                    />
                    <EuiSpacer size="m" />
                    <HorizontalCard
                        image={AvgRequestTime}
                        description="This is an example of an average request time chart"
                    />
                    <EuiSpacer size="m" />
                    <HorizontalCard
                        image={IndicesBreakdown}
                        description="This is an example of an indices breakdown pie chart"
                    />
                    <EuiSpacer size="m" />
                    <HorizontalCard
                        image={ResponseTimeVsRequestBodyLength}
                        description="This is an example of a chart showing cumulative response time vs cumulative request body length in KB"
                    />
                </EuiPanel>
            </EuiFlexItem>
            <EuiHorizontalRule margin="s" />
            <EuiFlexItem grow={2}>
                <EuiPanel>
                    <EuiTitle>
                        <h5>Custom audit log serializer</h5>
                    </EuiTitle>
                    <EuiText size="m">
                        <p>
                            You can write your own custom audit log serializer as a Java/Scala
                            class, add it to the ROR plugin class path and configure it through the
                            YAML settings.
                        </p>
                    </EuiText>
                    <EuiSpacer />
                    <EuiTitle>
                        <h5>More about this</h5>
                    </EuiTitle>
                    <EuiText size="m">
                        <p>
                            For more detailed explanation, see the{' '}
                            <a
                                target={'_blank'}
                                rel="noopener noreferrer"
                                href="https://github.com/beshu-tech/readonlyrest-docs/blob/master/elasticsearch.md#audit-logs"
                            >
                                official documentation project on Github
                            </a>
                            .
                        </p>
                    </EuiText>
                </EuiPanel>
            </EuiFlexItem>
            <EuiGlobalToastList toasts={toasts} dismissToast={removeToast} toastLifeTimeMs={6000} />
            {openAuditLogsModal && (
                <OverwriteAuditLogsModal
                    handleCloseModel={() => setOpenAuditLogsModal(false)}
                    handleConfirm={async () => {
                        await handleLoadAuditLogs(true);
                        setOpenAuditLogsModal(false);
                    }}
                />
            )}
        </EuiFlexGrid>
    );
}
