import React from 'react';
import { EuiFlexGrid, EuiFlexItem, EuiPanel, EuiText } from '@elastic/eui';

interface Props {
    image: string;
    description: string;
}

export const HorizontalCard = ({ image, description }: Props) => {
    return (
        <EuiPanel>
            <EuiFlexGrid columns={3} gutterSize="none">
                <EuiFlexItem>
                    <img src={image} alt={image.toString()} width="85%" />
                </EuiFlexItem>
                <EuiFlexItem grow={2}>
                    <EuiText size="s">
                        <p>{description}</p>
                    </EuiText>
                </EuiFlexItem>
            </EuiFlexGrid>
        </EuiPanel>
    );
};
