import { AxiosError } from 'axios';

interface HandleApiResponseErrorOptions {
  handleNon200Error?: (error: AxiosError) => void;
  handle404Error?: (error: AxiosError) => void;
  handleDefaultError?: (error: AxiosError) => void;
}

/**
 * Conventional way to handle axios errors, see https://axios-http.com/docs/handling_errors
 */
export function handleApiResponseError(
  error: AxiosError,
  { handleNon200Error, handle404Error, handleDefaultError }: HandleApiResponseErrorOptions
) {
  if (error.response) {
    // The request was made and the server responded with a status code
    // that falls out of the range of 2xx

    switch (error.response.status) {
      case 404: {
        if (handle404Error) {
          handle404Error(error);
        }
        break;
      }
      default: {
        if (handleNon200Error) {
          handleNon200Error(error);
        }
      }
    }
  } else if (error.request) {
    // The request was made but no response was received
    // `error.request` is an instance of XMLHttpRequest in the browser and an instance of
    // http.ClientRequest in node.js
    if (handleDefaultError) {
      handleDefaultError(error);
    }
  } else {
    // Something happened in setting up the request that triggered an Error
    if (handleDefaultError) {
      handleDefaultError(error);
    }
  }
  console.error(error.config);
}
