import React, { useEffect, useRef, useState } from 'react';
import './setttings.css';
import { getInfo, getSettings, getSettingsFile, postSettings, SettingsDto } from '../../api/pkpClient';
import {
  EuiButton,
  EuiConfirmModal,
  EuiFlexGroup,
  EuiFlexItem,
  EuiGlobalToastList,
  EuiOverlayMask,
  EuiPanel,
  EuiSpacer,
  EuiToolTip
} from '@elastic/eui';
import { AxiosResponse } from "axios";
import { sendCloseSettingsMessage, useHandleSettingsIframeMessage } from "../../shared/hooks/useHandleSettingsIframeMessage";
import { useToast } from "../../shared/hooks/useToast";
import Editor from "./editor";
import InfoHint from "./infoHint";


export default function Settings() {
  const {toasts, showToast, removeToast} = useToast()
  const [settings, setSettings] = useState('');
  const [showBeforeExitModal, setShowBeforeExitModal] = useState(false);
  const [showBeforeSaveModal, setShowBeforeSaveModal] = useState(false);
  const [isFormDirty, setIsFormDirtyState] = useState(false);
  let info: AxiosResponse | undefined = undefined
  const isFormDirtyRef = useRef(isFormDirty)
  const setIsFormDirty = (newState: boolean) => {
    setIsFormDirtyState(newState);
    isFormDirtyRef.current = newState
  }

  useHandleSettingsIframeMessage(() => {
    if (isFormDirtyRef.current) {
      setShowBeforeExitModal(true);
      return
    }
    sendCloseSettingsMessage()
    setShowBeforeExitModal(false)
  })

  // TODO: warning disabling can be avoided if we wrap the function in question in a useCallback() hook and pass it as dependency
  /* eslint-disable react-hooks/exhaustive-deps */
  useEffect(() => {
      (async () => await fetchSettings())();
    }, []
  );

  async function reloadSettings() {
    await fetchSettings()
    setIsFormDirty(false)
    showToast('success', 'Reloaded configuration')
  }

  async function loadDefaultSettings() {
    if (isFormDirty) {
      setShowBeforeSaveModal(true);
      return
    }
    await loadSettingsFromFile()
  }

  async function loadSettingsFromFile() {
    const formBeforeFetching = Object.assign('', settings)
    await fetchSettingsFromFile();
    if (formBeforeFetching !== settings) {
      setIsFormDirty(true)
    }

    setShowBeforeSaveModal(false)
    showToast('success', 'Loaded default settings from readonlyrest.yml')
  }

  async function makeWarningMessage(): Promise<string | undefined> {
    if (!info) {
      info = await getInfo()
    }
    if (!info.data.isProduction) {
      console.log("Not in production mode, skip checks.")
      return
    }
    let message: string | undefined = undefined
    if (info.data.isBuildExpired) {
      message = "This ReadonlyREST build (" + info.data.versionString + ") has expired. Please contact support@readonlyrest.com."
    } else if (!info.data.isEnterprise && !info.data.isPro) {
      message = "You need a ReadonlyREST Enterprise or PRO license to save to cluster-wide settings"
    }
    return message
  }

  async function saveSettings(): Promise<void> {
    const message = await makeWarningMessage()
    if (message) {
      showToast('danger', message)
      await fetchSettings(true)
      return
    }

    try {
      let response = await postSettings({ settings: settings } as SettingsDto)
      if (response.status >= 300 || response?.data?.status !== 'ok') {
        showToast('danger', 'Failed to save the security settings', response.data.message)
        if (response.data.message.includes("Current settings are already loaded")) {
          setIsFormDirty(false)
        }
        return
      }
      showToast('success', 'Successfully saved security settings.');
      setIsFormDirty(false)
    } catch (e) {
      showToast('danger', 'Elasticsearch error', JSON.stringify(e))
    }
  }

  async function fetchSettings(silent?: boolean): Promise<void> {
    let settingsResponse: any = await getSettings();
    if (settingsResponse.data?.status === 'empty') {
      if (!silent) {
        showToast('warning', 'No settings in index', 'Loaded from readonlyrest.yml')
      }
      return await fetchSettingsFromFile();
    }
    return setSettings(settingsResponse.data.message);
  }

  async function fetchSettingsFromFile(): Promise<void> {
    setSettings((await getSettingsFile()).data.message)
  }

  function onEditorChange(value: string | undefined): void {
    if (value) {
      setSettings(value);
    }
    if (!isFormDirty) {
      setIsFormDirty(true)
    }
  }

  return (
    <>
      <InfoHint />
      <EuiSpacer size={'s'}/>
      <EuiPanel paddingSize='none'>
        <Editor height={'70vh'}  value={settings} aria-label='Settings editor' onChange={onEditorChange} />
      </EuiPanel>
      <EuiSpacer/>
      <EuiFlexGroup justifyContent='flexStart'>
        <EuiFlexItem grow={false}>
          <EuiToolTip position='top' content='Save settings to cluster'>
            <EuiButton fill onClick={saveSettings}
                       aria-label='Save settings to cluster'>Save</EuiButton>
          </EuiToolTip>
        </EuiFlexItem>
        <EuiFlexItem grow={false}>
          <EuiToolTip position='top' content='Load default settings from readonlyrest.yml file'>
            <EuiButton color='danger' onClick={loadDefaultSettings}
                       aria-label='Load default settings from readonlyrest.yml file'>Reload from file</EuiButton>
          </EuiToolTip>
        </EuiFlexItem>
        <EuiFlexItem grow={false}>
          <EuiFlexGroup justifyContent='spaceEvenly'>
            <EuiFlexItem grow={false}>
              {isFormDirty &&
              <EuiToolTip position='top' content='Reload settings from cluster'>
                <EuiButton color='warning' onClick={reloadSettings}
                           aria-label='Reload settings from cluster'>Discard changes</EuiButton>
              </EuiToolTip>
              }
            </EuiFlexItem>
          </EuiFlexGroup>
        </EuiFlexItem>
      </EuiFlexGroup>
      <EuiGlobalToastList
        toasts={toasts}
        dismissToast={removeToast}
        toastLifeTimeMs={6000}
      />
      {showBeforeExitModal &&
      <EuiOverlayMask>
        <EuiConfirmModal style={{ position: "fixed", top: "20%", margin: "5% auto", left: 0, right: 0 }}
                         title={"Changes not saved"}
                         onCancel={() => setShowBeforeExitModal(false)}
                         onConfirm={() => sendCloseSettingsMessage()}
                         cancelButtonText="Cancel"
                         confirmButtonText="Exit without saving"
        >
          You have unsaved changes. Are you sure you want to exit without saving?
        </EuiConfirmModal>
      </EuiOverlayMask>
      }
      {showBeforeSaveModal &&
      <EuiOverlayMask>
        <EuiConfirmModal style={{ position: "fixed", top: "20%", margin: "5% auto", left: 0, right: 0 }}
                         title={"Changes not saved"}
                         onCancel={() => setShowBeforeSaveModal(false)}
                         onConfirm={() => loadSettingsFromFile()}
                         cancelButtonText="Cancel"
                         confirmButtonText="Reload anyway"
        >
          You have unsaved changes. Are you sure you want reload settings from file? Your changes will be lost.
        </EuiConfirmModal>
      </EuiOverlayMask>
      }
    </>
  );
}
