import axios, { AxiosResponse } from 'axios';
import { CONSTANTS } from '../constants';
import { prepareFileForUpload } from './auditDashboardLog';

const urlParams = new URLSearchParams(window.location.search);
export const basePath = urlParams.get('basePath') || '';

const pkpClient = axios.create({
  baseURL: CONSTANTS.API_URL,
  withCredentials: true,
});

export const pkpWeb = axios.create({
  baseURL: `${basePath}pkp/web/`,
});

export const kibanaApi = axios.create({
  baseURL: `${basePath}api/`
})

export interface SettingsDto {
  settings: string;
}

export async function getSettings(): Promise<AxiosResponse> {
  return pkpClient.get('settings');
}

export async function postSettings(settings: SettingsDto): Promise<AxiosResponse> {
  return pkpClient.post('settings', settings);
}

export async function getSettingsFile(): Promise<AxiosResponse> {
  return pkpClient.get('settings/file');
}

export async function getInfo(): Promise<AxiosResponse> {
  return pkpClient.get('info');
}

export async function importAuditLogDashboard(
  filePath: string,
  fileName: string,
  overwrite: boolean
) {
  try {
    const response = await pkpWeb.get(filePath);
    return await importFile(response.data, fileName, overwrite);
  } catch (e) {
    throw e;
  }
}

async function importFile(data: string, fileName: string, overwrite: boolean) {
  return kibanaApi.post(
    `saved_objects/_import?overwrite=${overwrite}`,
    prepareFileForUpload(data, fileName),
    {
      headers: {
        'kbn-xsrf': 'reporting',
        'Content-Type': 'multipart/form-data',
      },
    }
  );
}
