export function prepareFileForUpload(data: string, fileName: string): FormData {
  const file = createDashboardFileObject(data, fileName);
  const formData = new FormData();
  formData.append('file', file);
  return formData;
}

function createDashboardFileObject(data: string, filename: string): File {
  return new File([data], filename);
}
