import { useRef } from 'react';
import { Theme } from '../theme/themeProvider';

export const useHandleGetTheme = () => {
  const getTheme = () => {
    const theme: Theme | null = sessionStorage.getItem('rorTheme') as Theme;
    return theme || 'light';
  };
  const themeRef = useRef<Theme>(getTheme());

  return { theme: themeRef.current };
};
