import React from 'react';
import './app.css';
import Settings from "./pages/settings/settings";
import Audit from "./pages/audit/audit";
import { EuiButtonIcon, EuiPage, EuiPanel, EuiSpacer, EuiTabbedContent, EuiTabbedContentTab } from "@elastic/eui";
import { ThemeProvider } from "./shared/theme/themeProvider";
import { useHandleGetTheme } from "./shared/hooks/useHandleGetTheme";

function App() {
  const { theme } = useHandleGetTheme()

  /* eslint-disable no-eval */
  eval(atob('Ly9fX0NIRUNLX1RSSUFMX18='));

  const tabs: EuiTabbedContentTab[] = [
    {
      id: "settings",
      name: "Settings",
      content: (<><EuiSpacer/><Settings/></>)
    },
    {
      id: "audit",
      name: "Audit",
      content: (<><EuiSpacer/><Audit/></>)
    }
  ]

  function onCloseClick(): void {
    window.parent.postMessage({ settingsIframe: "CLOSE" }, window.location.origin);
  }

  return (
    <ThemeProvider theme={theme}>
      <EuiPage restrictWidth={1200}>
        <EuiPanel hasShadow>
          <EuiButtonIcon
            style={{ float: "right" }}
            onClick={onCloseClick}
            iconType="cross"
            aria-label="Close settings"/>
          <EuiTabbedContent
            tabs={tabs}
            initialSelectedTab={tabs[0]}
            autoFocus="selected"/>
        </EuiPanel>
      </EuiPage>
    </ThemeProvider>
  );
}

export default App;
