import React, { CSSProperties } from 'react';
import { EuiCallOut } from '@elastic/eui';

type OS = 'mac' | 'win' | 'linux';

const getOS = (): OS => {
    if (navigator.platform.indexOf('Mac') > -1) {
        return 'mac';
    } else if (navigator.platform.indexOf('Win') > -1) {
        return 'win';
    }
    return 'linux'
};

const styles: Record<string, CSSProperties> = {
    kbd: {
        backgroundColor: '#eee',
        borderRadius: '3px',
        border: '1px solid #b4b4b',
        boxShadow: '0 1px 1px rgba(0, 0, 0, .2), 0 2px 0 0 rgba(255, 255, 255, .7) inset',
        color: '#333',
        display: 'inline-block',
        fontSize: '.85em',
        fontWeight: 'bold',
        lineHeight: '1',
        padding: '2px 4px',
        whiteSpace: 'nowrap',
    },
};

const InfoHint = () => {
    const buttonMapping = (os: OS): { ctrl: string } => {
        switch (os) {
            case 'mac': {
                return {
                    ctrl: '',
                };
            }
            default: {
                return {
                    ctrl: 'Ctrl',
                };
            }
        }
    };
    return (
      <div>
      <EuiCallOut color="primary" size="s" iconType="indexPatternApp"
                  title="ReadonlyREST security settings editor">
        <div>
          Changes will be picked up by all Elasticsearch nodes automatically, no need to rolling-restart all the nodes. Use <kbd style={styles.kbd}>{buttonMapping(getOS()).ctrl}</kbd> +{' '}<kbd style={styles.kbd}>F</kbd> to find, or <kbd style={styles.kbd}>F1</kbd> to see all available editor options.
        </div>
      </EuiCallOut>

      </div>
    );
};

export default InfoHint;
